/* stylelint-disable selector-type-no-unknown */
.vcl-svg {
  --font-family: adobe-clean, 'Source Sans Pro', -apple-system, BlinkMacSystemFont, 'Segoe UI',
    Roboto, sans-serif !important;
  flex: 1;
  margin: 80px 0.2px 3px 0;
}

.vcl-svg text {
  font-family: var(--font-family);
}

.vcl-svg .axis-label {
  font: 1rem var(--font-family);
}
.visa-viz-d3-line-container,
.vcl-svg {
  width: 100%;
}

#line-chart {
  text-align: center;
}

.dialog-graph {
  width: 100%;
}

.bullet-point {
  margin: 25px 1px 3px 0;
}

.instory-text {
  margin: 80px 0.2px 3px 0;
  font-size: 16px;
  display: flex;
  flex-direction: column;
  line-height: 25px;
}

@media (max-width: 48em) {
  /* #LineChart {
    font-size: 2vmax;
  }
  .vcl-svg {
    height: 100vmax;
  } */
  .vcl-svg text {
    font-size: 28px;
  }

  .vcl-svg .axis-label {
    font: 2rem var(--font-family);
  }
}
